<template>
  <div class="option-log">
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">操作日志</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div >
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline>
            <Row>
              <FormItem label="操作时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择操作时间"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="操作用户：" prop="optUser">
                <Input v-model="formValidate.optUser" placeholder="请输入操作用户" style="width:200px"></Input>
              </FormItem>
              <FormItem label="ip地址：" prop="ipAddress">
                <Input v-model="formValidate.ipAddress" placeholder="请输入ip地址" style="width:200px"></Input>
              </FormItem>
              <FormItem label="操作描述：" prop="optDesc">
                <Input v-model="formValidate.optDesc" placeholder="请输入操作描述" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit(0, 1)">查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>
                    {{pageSize*(pageNum-1)+index+1}}
                  </td>
                  <td>{{item.optTime}}</td>
                  <td>{{item.optUser}}</td>
                  <td>{{item.optUrl}}</td>
                  <td>{{item.optDesc}}</td>
                  <td>{{item.optUnit}}</td>
                  <td>{{item.ipAddress}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
       <div class="flexCenter pageBox">
          <Page :total="rowCount" 
          :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
          :current.sync="currentpage"
          show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let obj = this.$route.query;
    if (obj.optUser){
      this.formValidate.optUser = obj.optUser;
      this.formValidate.optDesc = obj.optDesc;
    }
    this.handleSubmit(0, 1);
  },
  data() {
    return {
      pageSize:10,
      pageNum:1,
      rowCount:0,
      currentpage:1,
      tableHeader: [
        { name: "序" },
        { name: "操作时间" },
        { name: "操作用户" },
        { name: "操作地址" },
        { name: "操作描述" },
        { name: "所属部门" },
        { name: "ip地址" }
      ],
      tableBody: [
      ],
      formValidate: {
        permissionTime: ["",""], //
        optUser: "" ,//操作用户
        optDesc: "" ,//操作描述
        ipAddress: "" ,//ip地址
      },
      ruleValidate: {
      }
    };
  },
  methods: {
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        optTime1:this.formValidate.permissionTime[0],
        optTime2:this.formValidate.permissionTime[1],
        optUser: this.formValidate.optUser.trim() ,//操作用户
        optDesc: this.formValidate.optDesc.trim(),//操作描述
        ipAddress: this.formValidate.ipAddress.trim() ,//ip地址
        ifExacte:ifExacte,
      }
      this.axios
      .post(`/system/sysLog/listSearch/${pageNo}/${this.pageSize}`, data, {
          headers: { "Content-Type": "application/json" }
        })
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.records;
          this.rowCount = response.data.total;
        }
      })
      .catch(error => {});
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
  }
};
</script>
<style lang="scss" @scoped>
.option-log {
  .rightTopbtn {
    margin: 15px 4px;
    float: right;
  }
}
</style>



